import React, { Component } from 'react';
import { Form, Table } from 'antd';


//import fakeData from './fakeData';



//const dataList = new fakeData(3);


const columns = [
  {
    title: 'Name',
    dataIndex: 'id',
    key: 'id',
    render: text => <a >{text}</a>,
  },
  {
    title: 'Age',
    dataIndex: 'plan_name',
    key: 'age',
  },
  {
    title: 'Address',
    dataIndex: 'id',
    key: 'address',
  },
];
class FormDetails extends Component {

  constructor(props) {
    super(props);
    // Don't call this.setState() here!
    this.state = { data: null };
    fetch("http://localhost:9000/api/allClaim", {
      method: 'GET', // or 'PUT'
      headers:{
        'Content-Type': 'application/json'
      }
    }).then(res => res.json())
    .then(response => {
      //console.log(response.claimID)
      this.setState({ data: response.claimID});
    })
    .catch(error => console.error('Error:', error));
  }
  //
  // componentDidMount(){
  //   //console.log(dataList);
  //
  // }
  render() {

    return (
      <Form onSubmit={this.handleSubmit}>
        <Table columns={columns} dataSource={this.state.data}  rowKey="id"/>
      </Form>
    );
  }
}

const WrappedFormDetails = Form.create()(FormDetails);
export default WrappedFormDetails;
